import $ from "jquery";

// DOCUMENT READY...
$(function () {
  /*
  
  */
  (function () {
    /**
     *
     *  Device  (custom data attributes)
     *
     */

    let $wrap = $("#wrap");
    let detail = $.getDevice().detail;

    $wrap.wait(100).attr("data-device-detail", detail);
  })();
  /*
  
  */
  (function () {
    /**
     *
     *       
     *
     *   [URL  ]
     *   https:///?moveto=data-card= // $("[data-card='']")  
     *   https:///?moveto=section // $("#section")  
     *   https:///?moveto=card // $(".card")  
     *   https:///?moveto=card--.js-sticky // $(".js-sticky")   $(".card")  
     *   https:///?moveto=card--50 // 50px   $(".card")  
     *
     */
    setTimeout(() => {
      $.moveToParams();
    }, 1000);
  })();
  /*
  
  */
  (function () {
    /**
     *
     * () 
     *
     */

    $.disabledDoubleTab();
  })();
  /*
  
  */
  (function () {
    /**
     *
     * .SVG to XML 
     *
     */
    $('img[src*=".svg"]').makeSvg();
  })();
  /*
  
  */
  (function () {
    /**
     *
     * wrapper is ready
     *
     */
    let $wrap = $("#wrap");
    let $loding = $("#lodingWrap");

    $loding.remove();
    $wrap.css({
      opacity: 1,
      visibility: "visible",
    });
  })();
  /*
  
  */
  (function () {
    /**
     *
     *  
     *
     */
    $(".js-anchor").anchor();
  })();
  /*
    
    */
  (function () {
    /**
     *
     * form    
     *
     */
    let $wrap = $("#wrap");

    $wrap.formTamplate();
  })();
  /*
  
  */
  (function () {
    /**
     *
     *.js-is-appear FadeIn  
     *
     *  [html  ]
     *      // 
     *      <div class="isAppear">
     *
     *      //scroll.top  (: class   )
     *      <div class="isAppear50">
     *
     *  [CSS  ]
     *      div {opacity:0;transition:none}
     *      div.appear {opacity:1;transition:opacity 0.3s ease-in-out}
     *
     */

    var $wrap = $("[class*=js-is-appear]");

    $wrap.each(function () {
      var $this = $(this);
      var classAll = $this.attr("class").split(" ");

      //scroll.top  ,
      var top = filterList(classAll, function (className) {
        if (className.indexOf("is-appear") !== -1) {
          var num = className.replace(/[^0-9]/g, "");

          $this.removeClass(className); //removeClass
          return (num = num !== "" ? num : 70); //scroll.top  70 
        }
      })[0];

      //addClass('is-appear')
      scrollAction({
        target: $this,
        top: Number(top),
        scrollDownAction: function () {
          $this.addClass("appear");
        },
        scrollUpAction: function () {
          if (isReal == false) {
            $this.removeClass("is-appear");
          }
        },
      });
    });
  })();
  /*
  
  */
  (function () {
    /**
     *
     * TEXT 
     *
     */
    $(".js-clamp-1").clamp(1);
    $(".js-clamp-2").clamp(2);
    $(".js-clamp-3").clamp(3);
  })();
  /*
  
  */
  (function () {
    /**
     *
     *     
     *
     * [html  ]
     * <div data-loding-spinner="true"></div> //  
     * <div data-loding-spinner="false"></div> //  
     * <div data-loding-spinner="true dimmed"></div> //        ( : )
     * <div data-loding-spinner="true 1rem"></div> // rem      ( : 6rem)
     *
     */

    $("[data-loding-spinner]").lodingSpinner();
  })();
  /*
  
  */
  (function () {
    /**
     *
     *    
     *
     */

    let winH = window.innerHeight;
    let wrapH = $("#wrap").outerHeight();
    let $container = $("#container");
    let $btnTop;

    // windows   documnet 1.5  "TOP Button" Node 
    if (winH * 1.5 < wrapH) {
      $btnTop = $(`
            <aside id="aisdeBtnTop">
                <button class="btn btn-top js-anchor" data-anchor="#wrap" type="button"><span>TOP</span></button>
            </aside>`);

      $container.append($btnTop); //  
      isActive(); //  , 
      $btnTop.find(".js-anchor").anchor(); //    
    }

    // windows 1/3    "TOP Button" -
    let status;
    function isActive() {
      $(window).on("scroll", function () {
        let winT = $(this).scrollTop();

        if (winH / 3 < winT) {
          if (!status) $btnTop.addClass("is-active");
          status = true;
        } else {
          if (status) $btnTop.removeClass("is-active");
          status = false;
        }
      });
    }

    // 3     "TOP Button" 
    setInterval(function () {
      if (status) {
        $btnTop.removeClass("is-active");
        status = false;
      }
    }, 3000);
  })();
  /*
  
  */
  (function () {
    /**
     *
     * #footer   
     *
     */
    let $container = $("#container");
    let $footer = $("#footer");
    let footerH = $footer.height();
    let resizeHeight = () => {
      let afterContainerH = Math.abs(innerHeight - footerH);

      $container.css("min-height", afterContainerH);
    };

    resizeHeight();

    // window resize event
    window.addEventListener("optimizedResize", () => {
      resizeHeight();
    });
  })();
  /*
  
  */
}); // DOCUMENT READY...
